import React, { useRef, useLayoutEffect, useState } from 'react';
import FlowChart from './FlowChart';

interface WaterSupplySectionProps {
  collectorName: string;
  loops: { room?: string; flowRate?: number; resistance?: number; power?: number; regime?: string; usefulLength?: number; type?: string; innerDiameter?: number; device?: string; photo?: string | null; }[];
  photo?: string | null;
  containerRef?: React.Ref<HTMLDivElement>;
  forceReady?: boolean;
}

const WaterSupplySection: React.FC<WaterSupplySectionProps> = ({ collectorName, loops, photo, containerRef, forceReady }) => {
  const totalFlow = loops.reduce((sum, l) => sum + (l.flowRate || 0), 0);
  const maxResistance = Math.max(...loops.map(l => l.resistance ?? 0));
  const maxLoops = loops
    .map((l, idx) => ({ ...l, idx }))
    .filter(l => l.resistance === maxResistance && maxResistance > 0);

  // ---      ---
  const sectionRef = useRef<HTMLDivElement>(null);
  const cardsContainerRef = useRef<HTMLDivElement>(null);
  const [cardsStartY, setCardsStartY] = useState(0);
  useLayoutEffect(() => {
    if (sectionRef.current && cardsContainerRef.current) {
      const sectionRect = sectionRef.current.getBoundingClientRect();
      const cardsRect = cardsContainerRef.current.getBoundingClientRect();
      setCardsStartY(cardsRect.top - sectionRect.top);
    }
  }, [loops, photo, collectorName]);

  //   
  const coldLoops = loops.filter(l => l.type === '');
  //     
  const hotLoops = loops.filter(l => l.type === '');
  const recLoops = loops.filter(l => l.type === '');

  return (
    <div ref={el => {
      if (typeof containerRef === 'function') containerRef(el);
      else if (containerRef) (containerRef as React.MutableRefObject<HTMLDivElement | null>).current = el;
      sectionRef.current = el;
    }} style={{
      width: '1200px', 
      boxSizing: 'border-box', 
      margin: '40px auto 0 auto', 
      borderRadius: '12px',
      display: 'flex',
      flexDirection: 'column',
      position: 'relative'
    }}>
      {/*         >= 1596px */}
      {sectionRef.current && sectionRef.current.offsetHeight >= 1596 && (
        <div style={{
          position: 'absolute',
          top: '1596px',
          left: 0,
          width: '100%',
          borderTop: '2px dashed #bbb',
          zIndex: 10,
          pointerEvents: 'none',
          opacity: 0
        }} />
      )}
      <div style={{
        padding: '0 50px',
        display: 'flex',
        flexDirection: 'column',
        flex: '1 1 auto'
      }}>
        <div style={{
          background:'#c4c4c4',
          borderRadius:2,
          height:32,
          display:'flex',
          alignItems:'center',
          justifyContent:'space-between',
          padding: 0,
          marginBottom:18,
          width: '100%'
        }}>
          <span style={{fontWeight:700, color:'#fff', fontSize:16, marginLeft:24}}>GalfDesign</span>
          <span style={{color:'#fff', fontSize:14, marginRight:24}}> </span>
        </div>
        <div style={{padding: 0}}>
          <div style={{textAlign: 'center', marginBottom: 12, marginTop: 0}}>
            <h2 style={{fontSize: 22, fontWeight: 700, margin: 0, textAlign: 'center'}}></h2>
            <div style={{fontSize: 15, color: '#666', fontWeight: 400, marginTop: 4}}>
              {(() => {
                const now = new Date();
                const pad = (n: number) => n.toString().padStart(2, '0');
                return `${pad(now.getDate())}.${pad(now.getMonth()+1)}.${now.getFullYear()} ${pad(now.getHours())}:${pad(now.getMinutes())}`;
              })()}
            </div>
          </div>
          {photo && (
            <div style={{
              marginBottom: 18,
              display: 'flex',
              alignItems: 'center',
              width: '100%',
              boxSizing: 'border-box',
              justifyContent: 'space-between',
              gap: 0
            }}>
              <div style={{width: '100%', maxWidth: 500, minWidth: 0}}>
                <div style={{marginBottom: 16}}>
                  <b>      (, /,  30.13330.2016):</b>
                  <div style={{marginTop: 8, width: '100%', background: '#f9f9fb', borderRadius: 8, padding: '8px 8px'}}>
                    {[
                      {name: '', min: 4, max: 6},
                      {name: ' ', min: 6, max: 9},
                      {name: ' ()', min: 12, max: 18},
                      {name: '', min: 7, max: 12},
                      {name: '', min: 4, max: 6},
                      {name: ' ()', min: 7, max: 9},
                      {name: ' ', min: 9, max: 12},
                      {name: ' ', min: 6, max: 9},
                      {name: ' ', min: 9, max: 12},
                      {name: ' ', min: 9, max: 18},
                    ].map((item, idx) => {
                      const maxBar = 18;
                      const barMin = Math.round((item.min / maxBar) * 100);
                      const barMax = Math.round((item.max / maxBar) * 100);
                      const percent = Math.round((item.max / maxBar) * 100);
                      return (
                        <div key={item.name} style={{display:'flex',alignItems:'center',marginBottom:4}}>
                          <div style={{width: 120, fontSize: 13, color:'#222', flexShrink:0}}>{item.name}</div>
                          <div style={{flex:1, height: 12, position:'relative', background:'#e0e0e6', borderRadius: 6, margin: '0 6px', minWidth: 40}}>
                            <div style={{position:'absolute', left:0, top:0, height:'100%', borderRadius:6, background:'#0071e3', width: barMax + '%', opacity:0.25}}></div>
                            <div style={{position:'absolute', left:0, top:0, height:'100%', borderRadius:6, background:'#0071e3', width: barMin + '%'}}></div>
                          </div>
                          <div style={{width: 40, textAlign:'right', fontSize: 13, color:'#1976d2', fontWeight: 600}}>{item.min}{item.max} /</div>
                          <div style={{width: 38, textAlign:'left', fontSize: 12, color:'#888', marginLeft: 4}}>{percent}%</div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
              <div style={{flex: 1, textAlign: 'right', minWidth: 0, display: 'flex', justifyContent: 'flex-end'}}>
                <img src={photo} alt="" style={{height: 320, maxWidth: 500, borderRadius: 10, boxShadow: '0 2px 8px rgba(0,0,0,0.08)', objectFit: 'contain'}} />
              </div>
            </div>
          )}
          <div style={{marginBottom: 12}}>
            <b>:</b> {collectorName || '-'}
          </div>
          <div style={{marginBottom: 12}}>
            <b> </b>
          </div>
          <div style={{marginBottom: 18}}>
            <div style={{fontWeight:700, fontSize:18, margin:'18px 0 8px 0', textAlign:'left'}}>  </div>
            <table style={{width:'100%', borderCollapse:'collapse', fontSize:15, background:'#fff', boxSizing: 'border-box'}}>
              <thead>
                <tr style={{background:'#f5f6fa'}}>
                  <th style={{padding:'6px 8px', border:'1px solid #eee', width: '50px'}}></th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}></th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}></th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '70px'}}></th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee'}}></th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee'}}>. .</th>
                  <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}></th>
                </tr>
              </thead>
              <tbody>
                {coldLoops.map((l, idx) => (
                  <tr key={idx} style={idx % 2 === 1 ? {background:'#f5f6fa'} : {}}>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '50px'}}>{idx+1}</td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}>{l.room || '-'}</td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}>{l.device || '-'}</td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', background:'#2196f3', width:70}}></td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.usefulLength !== undefined ? l.usefulLength : '-'}</td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.innerDiameter !== undefined ? l.innerDiameter : '-'}</td>
                    <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.photo ? (
                      <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#4caf50" strokeWidth="2"><polyline points="5,11 9,15 15,7" fill="none"/></svg>
                    ) : (
                      <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#ff3b30" strokeWidth="2"><line x1="5" y1="5" x2="15" y2="15"/><line x1="15" y1="5" x2="5" y2="15"/></svg>
                    )}</td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div style={{fontSize: 13, color: '#888', marginTop: 4}}>*    </div>
          </div>
          <div style={{fontWeight:700, fontSize:18, margin:'32px 0 8px 0', textAlign:'left'}}>  </div>
          <table style={{width:'100%', borderCollapse:'collapse', fontSize:15, background:'#fff', boxSizing: 'border-box', marginBottom: 24}}>
            <thead>
              <tr style={{background:'#f5f6fa'}}>
                <th style={{padding:'6px 8px', border:'1px solid #eee', width: '50px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '70px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee'}}>. .</th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}></th>
              </tr>
            </thead>
            <tbody>
              {hotLoops.map((l, idx) => (
                <tr key={idx} style={idx % 2 === 1 ? {background:'#f5f6fa'} : {}}>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '50px'}}>{idx+1}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}>{l.room || '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}>{l.device || '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', background:'#ff3b30', width:70}}></td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.usefulLength !== undefined ? l.usefulLength : '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.innerDiameter !== undefined ? l.innerDiameter : '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.photo ? (
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#4caf50" strokeWidth="2"><polyline points="5,11 9,15 15,7" fill="none"/></svg>
                  ) : (
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#ff3b30" strokeWidth="2"><line x1="5" y1="5" x2="15" y2="15"/><line x1="15" y1="5" x2="5" y2="15"/></svg>
                  )}</td>
                </tr>
              ))}
            </tbody>
          </table>
          <div style={{fontWeight:700, fontSize:18, margin:'32px 0 8px 0', textAlign:'left'}}>  </div>
          <table style={{width:'100%', borderCollapse:'collapse', fontSize:15, background:'#fff', boxSizing: 'border-box', marginBottom: 24}}>
            <thead>
              <tr style={{background:'#f5f6fa'}}>
                <th style={{padding:'6px 8px', border:'1px solid #eee', width: '50px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '70px'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee'}}></th>
                <th style={{padding:'6px 8px', border:'1px solid #eee'}}>. .</th>
                <th style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}></th>
              </tr>
            </thead>
            <tbody>
              {recLoops.map((l, idx) => (
                <tr key={idx} style={idx % 2 === 1 ? {background:'#f5f6fa'} : {}}>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '50px'}}>{idx+1}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '200px'}}>{l.room || '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', width: '450px'}}>{l.device || '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center', background:'#ffd600', width:70}}></td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.usefulLength !== undefined ? l.usefulLength : '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.innerDiameter !== undefined ? l.innerDiameter : '-'}</td>
                  <td style={{padding:'6px 8px', border:'1px solid #eee', textAlign:'center'}}>{l.photo ? (
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#4caf50" strokeWidth="2"><polyline points="5,11 9,15 15,7" fill="none"/></svg>
                  ) : (
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" stroke="#ff3b30" strokeWidth="2"><line x1="5" y1="5" x2="15" y2="15"/><line x1="15" y1="5" x2="5" y2="15"/></svg>
                  )}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <div style={{ width: '100%', textAlign: 'center', fontWeight: 700, fontSize: 20, margin: '24px 0 16px 0', letterSpacing: 1 }}></div>
        <div ref={cardsContainerRef}>
        {(() => {
          const cardHeightPx = 113;
          const cardGapPx = 21;
          const lineYAbsolute = 1596;
          const lineY = lineYAbsolute - cardsStartY;
          const cardsPerRow = 4;
          //   : , , 
          const typeOrder = { '': 1, '': 2, '': 3 };
          const sortedLoops = [...loops].sort((a, b) => {
            const aType = (a.type || '') as keyof typeof typeOrder;
            const bType = (b.type || '') as keyof typeof typeOrder;
            return (typeOrder[aType] || 99) - (typeOrder[bType] || 99);
          });
          let rows = [];
          for (let i = 0; i < sortedLoops.length; i += cardsPerRow) {
            rows.push(sortedLoops.slice(i, i + cardsPerRow));
          }
          const rowHeightPx = cardHeightPx + cardGapPx;
          let blocks: React.ReactElement[] = [];
          let currentHeight = 0;
          rows.forEach((row, rowIdx) => {
            if (currentHeight < lineY && currentHeight + rowHeightPx > lineY) {
              blocks.push(
                <div key={'break-row-' + rowIdx} style={{ width: '100%', height: (lineY - currentHeight) + cardGapPx + 'px' }} />
              );
              blocks.push(
                <div key={'header-' + rowIdx} style={{
                  background: 'rgb(196, 196, 196)',
                  borderRadius: '2px',
                  height: '32px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  padding: '0px',
                  marginBottom: '18px',
                  width: '100%',
                  pageBreakBefore: 'always',
                  breakBefore: 'page'
                }}>
                  <span style={{ fontWeight: 700, color: 'rgb(255, 255, 255)', fontSize: '16px', marginLeft: '24px' }}>GalfDesign</span>
                  <span style={{ color: 'rgb(255, 255, 255)', fontSize: '14px', marginRight: '24px' }}> </span>
                </div>
              );
              currentHeight = lineY;
            }
            blocks.push(
              <div key={'row-' + rowIdx} style={{ display: 'flex', flexWrap: 'nowrap', gap: cardGapPx, justifyContent: 'center', marginBottom: rowIdx === rows.length-1 ? 0 : cardGapPx }}>
                {row.map((l, idx) => (
                  <div key={idx} className='card' style={{
                    background: 'transparent',
                    borderRadius: 14,
                    padding: '12px',
                    width: '60mm',
                    height: '30mm',
                    border: `4px solid ${l.type === '' ? '#2196f3' : l.type === '' ? '#ff3b30' : l.type === '' ? '#ffd600' : '#111'}`,
                    marginBottom: 0,
                    boxShadow: 'none',
                    display: 'flex',
                    flexDirection: 'column',
                    fontSize: 15,
                    position: 'relative',
                    pageBreakInside: 'avoid',
                    breakInside: 'avoid'
                  }}>
                    <div style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'flex-start',
                      marginBottom: '8px'
                    }}>
                      <div style={{
                        fontWeight: 700,
                        fontSize: 16,
                        color: '#333',
                        maxWidth: '100%',
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        textAlign: 'left',
                        paddingLeft: '4px'
                      }}>
                        {(l.room ? l.room : ` ${rowIdx * cardsPerRow + idx + 1}`).toUpperCase()}
                      </div>
                    </div>
                    <div style={{
                      flex: 1,
                      display: 'flex',
                      flexDirection: 'column',
                      justifyContent: 'space-between'
                    }}>
                      <div style={{ 
                        fontSize: 18,
                        color: '#333',
                        marginBottom: '4px',
                        fontWeight: 500,
                        textAlign: 'left',
                        marginTop: '8px',
                        paddingLeft: '4px'
                      }}>
                        {l.device || '-'}
                      </div>
                    </div>
                    <div style={{
                      width: '100%',
                      display: 'flex',
                      flexDirection: 'row',
                      justifyContent: 'space-between',
                      alignItems: 'flex-end',
                      marginTop: 'auto',
                      padding: '0 0 8px 8px',
                      boxSizing: 'border-box'
                    }}>
                      <div style={{
                        position: 'absolute',
                        right: 8,
                        top: '50%',
                        transform: 'translateY(-50%)',
                        opacity: 0.8,
                        zIndex: 1,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                      }}>
                        <img src={process.env.PUBLIC_URL + '/logocart.png'} alt="logo" style={{ height: 80 }} />
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            );
            currentHeight += rowHeightPx;
          });
          return (
            <div style={{ 
              width: '100%', 
              display: 'flex', 
              flexDirection: 'column', 
              alignItems: 'center', 
              marginTop: 28, 
              marginBottom: blocks.length > 0 ? 50 : 0, 
              pageBreakInside: 'avoid', 
              breakInside: 'avoid',
              background: 'transparent'
            }}>
              {blocks}
            </div>
          );
        })()}
        </div>
        {/*     */}
        <div style={{ width: '100%', marginTop: '40px', marginBottom: '40px' }}>
          <div style={{ fontWeight: 700, fontSize: 20, marginBottom: '20px', textAlign: 'center' }}> </div>
          <div style={{ 
            display: 'grid', 
            gridTemplateColumns: 'repeat(auto-fill, minmax(200px, 1fr))', 
            gap: '20px',
            padding: '0 20px'
          }}>
            {loops.filter(l => l.photo).map((loop, index) => (
              <div key={index} style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                gap: '8px'
              }}>
                <div style={{
                  fontSize: '14px',
                  color: '#666',
                  textAlign: 'center',
                  width: '100%'
                }}>
                  <div style={{ 
                    fontWeight: 600, 
                    padding: '4px 8px',
                    borderRadius: '4px',
                    display: 'inline-block',
                    background: loop.type === '' ? '#2196f3' : 
                              loop.type === '' ? '#ff3b30' : 
                              loop.type === '' ? '#ffd600' : '#f5f5f5',
                    color: loop.type === '' ? '#333' : '#fff'
                  }}>
                    {loop.type || '-'}
                  </div>
                  <div style={{ marginTop: '4px' }}>{loop.room || '-'}</div>
                  <div>{loop.device || '-'}</div>
                </div>
                <div style={{
                  minWidth: '200px',
                  maxWidth: '100%',
                  height: '200px',
                  border: '1px solid #ddd',
                  borderRadius: '8px',
                  overflow: 'auto',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  background: '#f5f5f5'
                }}>
                  <img 
                    src={loop.photo || ''} 
                    alt={`  ${index + 1}`}
                    style={{
                      height: '200px',
                      width: 'auto',
                      objectFit: 'contain',
                      objectPosition: 'center',
                      borderRadius: '8px',
                      display: 'block',
                      margin: '0 auto'
                    }}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default WaterSupplySection; 