import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

interface FlowChartProps {
  loops: { room?: string; flowRate?: number }[];
  forceReady?: boolean;
}

const FlowChart: React.FC<FlowChartProps> = ({ loops, forceReady }) => {
  const [isReady, setIsReady] = useState(!!forceReady);

  useEffect(() => {
    if (forceReady) {
      setIsReady(true);
      return;
    }
    const timer = setTimeout(() => {
      setIsReady(true);
    }, 300); // 300ms    

    return () => clearTimeout(timer);
  }, [loops, forceReady]); //    

  const maxFlow = Math.max(...loops.map(l => l.flowRate ?? 0), 1);
  const percentData = loops.map(l => maxFlow ? ((l.flowRate ?? 0) / maxFlow) * 100 : 0);
  const data = {
    labels: loops.map((l, idx) => l.room ? l.room : ` ${idx + 1}`),
    datasets: [
      {
        label: ', /',
        data: loops.map(l => l.flowRate ?? 0),
        backgroundColor: '#0071e3',
        borderRadius: 6,
        maxBarThickness: 32,
        yAxisID: 'y',
      },
      {
        label: '',
        data: percentData,
        backgroundColor: 'rgba(0,113,227,0.15)',
        borderRadius: 6,
        maxBarThickness: 32,
        yAxisID: 'y1',
        hidden: true //    ,  
      }
    ],
  };
  const options = {
    responsive: true,
    animation: forceReady ? (false as const) : { duration: 800 },
    plugins: {
      legend: { display: false },
      title: { display: false },
      tooltip: { enabled: true },
    },
    scales: {
      x: {
        ticks: { color: '#222', font: { size: 13, weight: 'bold' as const } },
        grid: { display: false },
      },
      y: {
        beginAtZero: true,
        ticks: { color: '#222', font: { size: 13 } },
        grid: { color: '#eee' },
        title: { display: true, text: '/', color: '#222', font: { size: 13 } },
        min: 0,
        max: 5,
      },
      y1: {
        type: 'linear' as const,
        position: 'right' as const,
        beginAtZero: true,
        grid: { drawOnChartArea: false },
        min: 0,
        max: 100,
        ticks: {
          color: '#0071e3',
          font: { size: 13 },
          callback: (tickValue: string | number) => Number(tickValue) + '%'
        },
        title: { display: true, text: '%  .', color: '#0071e3', font: { size: 13 } },
      }
    },
    maintainAspectRatio: false,
  };
  return (
    <div style={{width: '100%', maxWidth: 500, minWidth: 0, height: 320, borderRadius: 10, padding: 8}}>
      {isReady ? (
        <Bar data={data} options={options} height={320} />
      ) : (
        <div style={{width: '100%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center', color: '#666'}}>
          ...
        </div>
      )}
    </div>
  );
};

export default FlowChart; 